$main-color: #fc6161;
$main-color-hover: #d83939;
$background-color: #f2f2f2;
$green: #119e00;
$light-gray: rgb(150, 150, 150);

$font-weight: 500;

$basic-box-shadow: 0px 1px 10px -7px rgba(66, 68, 90, 1);
$hover-box-shadow: 4px 4px 25px -15px rgba(66, 68, 90, 1);
$thin-border: 1px solid rgb(238, 238, 238);
$section-border: 1px solid rgb(223, 223, 223);
$border-radius: 10px;

$content-width-limit: 1800px;

$section-margin-top: 5rem;

$breakpoints: (
    smartphone : (max-width : 460px),
    smartphone-large : (max-width : 640px),
    tablet : (max-width : 800px),
    tablet-large : (max-width : 1024px),
    desktop-small : (max-width : 1280px),
    desktop-medium : (max-width : 1600px),
    desktop-large : (max-width : 1900px),
)
